import { ChangeDetectionStrategy, ChangeDetectorRef, Component, DoCheck } from "@angular/core";
import { User } from "../../model/user";
import { UserStoreService } from "../../store/services/user-store.service/user-store.service";

@Component({
  selector: "app-home-page",
  templateUrl: "./home-page.component.html",
  styleUrls: ["./home-page.component.less"],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class HomePageComponent implements DoCheck {
  constructor(private userStore: UserStoreService,
              private cdr: ChangeDetectorRef) { }

  user: User | {};

  ngDoCheck(): void {
    this.userStore.loadUserInfo().subscribe(
      (user) => {
        this.user = user;
        this.cdr.markForCheck();
      });
  }

}
